<template>
  <el-row style="marging-bottom: 20px">
    <el-col :span="4">
      <el-form-item label="成绩输入模式">
        <el-tag effect="plain" size="large">{{
          mode === 0 ? "期中" : mode === -1 ? "待检测" : "期末"
        }}</el-tag>
      </el-form-item>
    </el-col>
    <el-col :span="3" :push="15">
      <el-button type="text" @click="openInst">使用说明</el-button>
    </el-col>
  </el-row>

  <el-upload
    ref="upload"
    v-for="(text, i) in ['请上传钉钉模板', '请上传成绩文件']"
    :key="text"
    :limit="1"
    drag
    class="avatar-uploader"
    :on-change="
      (file) => {
        handleChange(file, i);
      }
    "
    :on-remove="
      (file) => {
        handleRemove(file, i);
      }
    "
    :on-exceed="
      (files) => {
        handleExceed(files[0], i);
      }
    "
    style="width: 80%; margin-left: 10%; margin-bottom: 10px"
    :auto-upload="false"
  >
    <!-- <el-icon class="avatar-uploader-icon"><Plus /></el-icon> -->
    <el-icon class="avatar-uploader-icon"
      ><Check v-if="uploadState[i].ok" /><Plus v-else
    /></el-icon>
    <template #tip>
      <div class="el-upload__tip">
        {{ uploadState[i].ok ? uploadState[i].name : text }}
      </div>
    </template>
  </el-upload>
  <el-button :loading="loading" type="primary" @click="handleClick"
    >下载文件</el-button
  >
</template>

<script>
import { Plus, Check } from "@element-plus/icons-vue";
import { ref } from "@vue/reactivity";
import { read, write, utils } from "xlsx"; // 注意处理方法引入方式
import { ElMessageBox, genFileId } from "element-plus";

// 将一个sheet转成最终的excel文件的blob对象，然后利用URL.createObjectURL下载
function sheet2blob(sheet, sheetName) {
  sheetName = sheetName || "sheet1";
  var workbook = {
    SheetNames: [sheetName],
    Sheets: {},
  };
  workbook.Sheets[sheetName] = sheet;
  // 生成excel的配置项
  var wopts = {
    bookType: "xlsx", // 要生成的文件类型
    bookSST: false, // 是否生成Shared String Table，官方解释是，如果开启生成速度会下降，但在低版本IOS设备上有更好的兼容性
    type: "binary",
  };
  var wbout = write(workbook, wopts);
  var blob = new Blob([s2ab(wbout)], { type: "application/octet-stream" });
  // 字符串转ArrayBuffer
  function s2ab(s) {
    var buf = new ArrayBuffer(s.length);
    var view = new Uint8Array(buf);
    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
    return buf;
  }
  return blob;
}
/**
 * 通用的打开下载对话框方法，没有测试过具体兼容性
 * @param url 下载地址，也可以是一个blob对象，必选
 * @param saveName 保存文件名，可选
 */
function openDownloadDialog(url, saveName) {
  if (typeof url == "object" && url instanceof Blob) {
    url = URL.createObjectURL(url); // 创建blob地址
  }
  var aLink = document.createElement("a");
  aLink.href = url;
  aLink.download = saveName || ""; // HTML5新增的属性，指定保存文件名，可以不要后缀，注意，file:///模式下不会生效
  var event;
  if (window.MouseEvent) event = new MouseEvent("click");
  else {
    event = document.createEvent("MouseEvents");
    event.initMouseEvent(
      "click",
      true,
      false,
      window,
      0,
      0,
      0,
      0,
      0,
      false,
      false,
      false,
      false,
      0,
      null
    );
  }
  aLink.dispatchEvent(event);
}

export default {
  components: {
    Plus,
    Check,
  },
  setup() {
    const mode = ref(-1);
    const upload = ref();
    const activePage = ref(1);
    const loading = ref(false);
    let absentStu = [];
    const uploadState = ref([
      { name: "", ok: false, file: undefined },
      { name: "", ok: false, file: undefined },
    ]);

    let totalNameList = [];

    const handleChange = (file, i) => {
      if (checkType(file.raw) === false) {
        upload.value[i].clearFiles();
        return;
      }
      uploadState.value[i].name = file.name;
      uploadState.value[i].ok = true;
      handleFile(file, i);
    };

    const handleFile = (file, i) => {
      if (file.raw) file = file.raw;
      const fileReader = new FileReader();
      fileReader.onload = (ev) => {
        try {
          loading.value = true;
          const data = ev.target.result;
          const workbook = read(data, { type: "binary" });
          if (i === 0) {
            uploadState.value[i].file = workbook.Sheets[workbook.SheetNames[0]];
          } else if (workbook.SheetNames.includes("年段")) {
            mode.value = 0;
            uploadState.value[i].file = workbook.Sheets["年段"];
            console.log(workbook.Sheets["年段"]);
          } else {
            mode.value = 1;
            uploadState.value[i].file = workbook.Sheets[workbook.SheetNames[0]];
          }
          loading.value = false;
        } catch (e) {
          ElMessageBox.alert("错误:" + e);
          loading.value = false;
          return false;
        }
      };
      fileReader.readAsBinaryString(file);
    };

    const handleRemove = (file, i) => {
      uploadState.value[i].ok = false;
      uploadState.value[i].file = undefined;
      if(i === 1) {
        mode.value = -1
      }
    };

    const columnMapList = [
      {
        学号: "A",
        语文: "F",
        数学: "G",
        英语: "H",
        物理: "I",
        化学: "J",
        生物: "N",
        政治: "K",
        历史: "L",
        地理: "M",
        技术: "O",
        外语: "",
        年级总分排名: "S",
        语文年级排名: "",
        数学年级排名: "",
        英语年级排名: "",
        物理年级排名: "",
        化学年级排名: "",
        生物年级排名: "",
        政治年级排名: "",
        历史年级排名: "",
        地理年级排名: "",
        技术年级排名: "",
        外语年级排名: "",
      },
      {
        学号: "A",
        语文: "F",
        数学: "J",
        英语: "N",
        物理: "R",
        化学: "V",
        生物: "Z",
        政治: "AD",
        历史: "AH",
        地理: "AL",
        技术: "AP",
        外语: "AT",
        年级总分排名: "CR",
        语文年级排名: "H",
        数学年级排名: "L",
        英语年级排名: "P",
        物理年级排名: "T",
        化学年级排名: "X",
        生物年级排名: "AB",
        政治年级排名: "AF",
        历史年级排名: "AJ",
        地理年级排名: "AN",
        技术年级排名: "AR",
        外语年级排名: "AV",
      },
    ];

    const handleExceed = (file, i) => {
      if (!checkType(file)) {
        return;
      }
      upload.value[i].clearFiles();
      file.uid = genFileId();
      upload.value[i].handleStart(file);
      uploadState.value[i].name = file.name;
      handleFile(file, i);
    };

    const handleClick = () => {
      if (!uploadState.value[0].ok) {
        ElMessageBox.alert("未输入钉钉模板");
        return;
      }
      if (!uploadState.value[1].ok) {
        ElMessageBox.alert("未输入学生成绩文件");
        return;
      }
      if (loading.value == true) return;
      absentStu = [];
      loading.value = true;
      const classNo = uploadState.value[0].file["B4"].v.replace(/[^0-9]+/g, "");
      const columnList = Object.keys(uploadState.value[0].file)
        .filter((key) => {
          return key.match(/^[A-Z]+3$/);
        })
        .map((key) => {
          const columnMap = columnMapList[mode.value];
          return {
            raw: key.replace(/[0-9]+/g, ""),
            to: columnMap[uploadState.value[0].file[key].v],
          };
        });

      const nameList = Object.keys(uploadState.value[0].file)
        .filter((key) => {
          if (key === "C3") return false;
          return key.match(/C[0-9]+/);
        })
        .map((key) => {
          return uploadState.value[0].file[key].v;
        });

      totalNameList = Object.keys(uploadState.value[1].file)
        .filter((key) => {
          if (["C1", "C2"].includes(key) && mode.value === 0) return false;
          if (["C1", "C2", "C3"].includes(key) && mode.value === 1)
            return false;
          return key.match(/^C[0-9]+$/);
        })
        .map((key) => {
          return uploadState.value[1].file[key].v;
        });

      new Promise((resolve, reject) => {
        nameList.forEach((name, i) => {
          let row = find(classNo, name);
          if (row == -1) {
            absentStu.push(name);
            return;
          }
          columnList.forEach((column) => {
            if (["B", "C", "O"].includes(column.raw)) return;
            if (column.to === "") return;
            if (row == -1) {
              uploadState.value[0].file[`${column.raw}${i + 4}`] = {
                s: "",
                v: "",
                t: "",
              };
            } else {
              let cell = uploadState.value[1].file[`${column.to}${row}`];
              if (cell) {
                uploadState.value[0].file[`${column.raw}${i + 4}`] = {
                  v: cell.v,
                  t: cell.t,
                  w: cell.w,
                };
              }
            }
          });
        });
        resolve();
      }).then(() => {
        loading.value = false;
        let name = uploadState.value[0].name.split(".");

        ElMessageBox.alert(`${absentStu}未参加考试.`, "提示", {
          confirmButtonText: "好的",
          callback: () => {
            openDownloadDialog(
              sheet2blob(uploadState.value[0].file, "成绩单模板1"),
              `${name[0]}-生成.${name[1]}`
            );
          },
        });
      });
    };

    const find = (classNo, name) => {
      for (var i = 0; i < totalNameList.length; i++) {
        if (totalNameList[i] !== name) {
          continue;
        }
        if (
          mode.value === 1 &&
          classNo === uploadState.value[1].file[`D${i + 4}`].v
        ) {
          return i + 4;
        } else if (
          mode.value === 0 &&
          classNo == uploadState.value[1].file[`B${i + 3}`].v
        ) {
          return i + 3;
        }
      }
      return -1;
    };

    const checkType = (rawFile) => {
      if (rawFile.type !== "application/vnd.ms-excel") {
        ElMessageBox.alert("必须是excel类型的文件");
        return false;
      }
      return true;
    };

    const openInst = () => {
      ElMessageBox({
        title: "使用说明",
        dangerouslyUseHTMLString: true,
        message: `
          <div>1. 从钉钉上下载模板文件<div>
          <div>2. 上传钉钉模板文件和学生成绩文件<div>
          <div>3. 点击下载文件</div>
          <div>4. 将下载的文件上传到钉钉成绩录入。</div>
         <a href="https://github.com/HGGshiwo/scoremanager">项目地址</a>`,
      });
    };

    return {
      activePage,
      handleChange,
      handleRemove,
      handleExceed,
      uploadState,
      handleClick,
      loading,
      openInst,
      upload,
      mode,
    };
  },
};
</script>


<style scoped>
.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>